import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { catchError, map } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import { throwError, BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  public userLoggedData: any;
  constructor(
    private http: HttpClient,
    private router: Router,
  ) { }
  readonly base_url = environment.api_url;
  public userData: BehaviorSubject<object> = new BehaviorSubject<object>({});

  httpOptions: any;
  userType: any;
  setHttpOptions() {
    this.httpOptions = {
      headers: new HttpHeaders({
        'Authorization': localStorage.getItem('token') ? 'Bearer ' + localStorage.getItem('token') : '',
        'Accept-Language': 'en'
      })
    };
  }

  // To make HTTP GET request
  commonGetCall(req_url, req_data?) {

    // Set http Options
    this.setHttpOptions();
    // make url
    const url = `${this.base_url}${req_url}`;
    // make request

    return this.http.get(url, { headers: this.httpOptions.headers, params: req_data })
      .pipe(
        map((res: any) => {
          return res;
        }),
        catchError(
          err => {
            if ((err.status && err.status === 401)) {
              this.deleteLocalConfig();
              if (req_url.indexOf('admin') !== -1) {
                this.redirectAdminLogin();
              } else {
                this.redirectLogin();
              }
            }
            return throwError(err);
          }
        ));
  }

  // To make HTTP POST request
  commonPostCall(req_url, req_data) {

    // Set http Options
    this.setHttpOptions();
    // make url
    const url = `${this.base_url}${req_url}`;
    // make request
    return this.http.post(url, req_data, this.httpOptions)
      .pipe(
        map((res: any) => res),
        catchError(
          err => {
            if ((err.status && err.status === 401)) {
              this.deleteLocalConfig();
              if (req_url.indexOf('admin') !== -1) {
                this.redirectAdminLogin();
              } else {
                this.redirectLogin();
              }
            }
            return throwError(err);
          }
        ));
  }

  shareUserData(data: object) {
    this.userData.next(data);
  }

  setLocalConfig(data): void {
    localStorage.setItem('userData', data.userData);
    localStorage.setItem('token', data.token);
    data.uid ? localStorage.setItem('uid', data.uid) : null;
  }

  getUserData(): void {
    const userData = localStorage.getItem('userData');
    if (userData != 'undefined' && userData != "" && userData != null) {
      this.shareUserData(JSON.parse(userData));
      return JSON.parse(userData);
    }
  }

  logout() {
    if (!localStorage.getItem('token')) {
      this.redirectLogin();
      return;
    }
    // this.loaderService.display(true);
    this.commonPostCall("auth/logout", {})
      .subscribe(res => {
        this.deleteLocalConfig();
        this.redirectLogin();
        // this.loaderService.display(false);
        // this.alert.success("You have been logged out successfully");
      }, err => {
        this.deleteLocalConfig();
        this.redirectLogin();
        // this.loaderService.display(false);
        // this.alert.success("You have been logged out successfully");
      })
  }

  adminLogout() {
    if (!localStorage.getItem('token')) {
      this.redirectAdminLogin();
      return;
    }
    // this.loaderService.display(true);
    this.commonPostCall("admin/logout", {})
      .subscribe(res => {
        this.deleteLocalConfig();
        this.redirectAdminLogin();
        // this.loaderService.display(false);
        // this.alert.success("You have been logged out successfully");
      }, err => {
        this.deleteLocalConfig();
        this.redirectAdminLogin();
        // this.loaderService.display(false);
        // this.alert.success("You have been logged out successfully");
      })
  }

  deleteLocalConfig(): void {
    localStorage.removeItem('userData');
    localStorage.removeItem('token');
    localStorage.removeItem('uid');
  }

  redirectLogin() {
    this.router.navigate(['/user/login']);
  }

  redirectAdminLogin() {
    this.router.navigate(['/admin/login']);
  }
  getUserUid() {
    const uid = localStorage.getItem('uid');
    return uid != undefined ? uid : false;
  }
  getToken() {
    return localStorage.getItem("token")
  }
  isLoggednIn() {
    return this.getToken() !== null;
  }
}
